.App {
  text-align: center;
}

.shopping-cart {
  display: none;
}

.shopping-cart-active {
  display: flex;
  flex-direction: column;
  background-color: #f8ebe6;
  width: 20%;
  height: 100vh;
  align-self: end;
  float: right;
  position: absolute;
  left: 80%;
}

.links {
  display: flex;
  justify-content: space-evenly;
}
.shopButt {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-info {
  margin-left: 10px;
}

.cart-item {
  display: flex;
  margin: 20px;
}
.cart-info {
  font-size: 1.25rem;
}
nav {
  display: flex;
  flex-basis: 0;
  align-items: center;
  justify-content: space-evenly;
  background-color: #ffdae8;
  font-family: "dk-lemon-yellow-sun";
}
nav button {
  margin: 10px;
  background-image: url(./routes/imgs/shopping-cart.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 30px;
  background-color: #f8ebe6;
  border-radius: 30%;
  width: 50px;
  height: 50px;
}

nav button span {
  display: none;
}
nav a {
  font-size: 2.441rem;
  margin: 20px;
}

nav h1 {
  font-size: 3.052rem;
}
.item-field {
  display: flex;
}
.item {
  width: 30vw;
  height: 30vh;
}

.item img {
  width: 80%;
  height: 80%;
}

.shop-page h1 {
  text-align: center;

  font-family: "snow-white";
}

.item-page {
  margin: 50px;
  display: flex;
  justify-content: space-evenly;
}

.item-info {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.item-add {
  background-color: #f8ebe6;
  font-family: "crewniverse";
  width: 10vw;
  height: 5vh;
}

.homepage h1 {
  font-family: "snow-white";
}

.items {
  text-decoration: none;
  color: black;
}

.items p {
  font-weight: bolder;
}

