.items {
  text-decoration: none;
  color: black;
}

.items p {
  font-weight: bolder;
}



.item-field {
  display:flex;
  justify-content: center;
}

.item-field p {
  font-family: "Roboto", sans-serif;
}
.item {
  width: 15vw;
  height: 30vh;
}

.item-container{
  width:100%;

}

.item img {
  width: 80%;
  height: 80%;
}

.shop-page {
  width: 100vw;
  height: 100vh;
  background-image: url(../routes/imgs/desktop-1.jpg);
}
.shop-page h1 {
  margin: 0px;
  text-align: center;

  font-family: "snow-white";
}

.item-page {
  margin: 50px;
  display: flex;
  justify-content: space-evenly;
}

.item-info {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.add-button{
  background-color: #f8ebe6;
font-family: "crewniverse";
width: 10vw;
height: 5vh;
margin:0px auto;
}
