.homepage {
  color: rgba(173, 55, 228, 0.849);
  min-width: 100%;
  min-height: 100vh;
  background: url(../routes/imgs/desktop-1.jpg);
}

.twitch {
  background-image: url("../routes/imgs/twitch.png");
}

.homepage h1 {
  font-size: 3.052rem;
  font-family: "snow-white";
  margin: 0px;
}

.homepage p {
  font-family: "crewniverse";
  font-size: 2.953rem;
}

.shop-now {
  font-family: "dk-lemon-yellow-sun";
  width: 10vw;
  height: 10vh;
  font-size: 4.052rem;
  text-decoration: none;
  color: white;
  background: rgba(173, 55, 228, 0.849);
  box-shadow: 5px 5px 5px #b72a5d;
}

.shop-now:hover {
background:white;
color:rgba(173, 55, 228, 0.849);
}

.home-banner {
  display: flex;
  justify-content: space-around;
}

.avatar {
  opacity: 80%;
  width: 40vw;
  height: 80vh;
  transform:translateY(-150px);
}

.icons {
  margin: 64px 30px 25px 0px;
  width: 64px;
  height: 64px;
  transition: transform  ease-in .1s;
}


.icons:hover{
  transform:translateY(-20px);
}